                <i class="el-icon-view"></i>
                <span v-if="showProps">收起属性</span>
                <span v-else>查看属性</span>
            </el-button>
        </div>
        <!-- 项目详情，仅在单页展示 -->
        <template v-if="showDetail">
            <!-- 详细字段列表 -->
            <div class="m-detail"  v-if="showProps">
                <template v-for="(val, key) in data">
                    <span class="u-prop" :key="key" v-if="propsFilter(key)">
                        <el-tooltip v-if="showKeyLabel(key)" effect="dark" :content="key" placement="top">
                            <em>{{ showKeyLabel(key) }}</em>
                        </el-tooltip>
                        <em v-else>{{ key }}</em>
                        {{ val }}
                    </span>
                </template>
            </div>
            <!-- 项目操作 -->
            <div class="m-operation" v-if="isLogin">
                <el-button
                    v-if="!star_id"
                    class="u-button u-star"
                    icon="el-icon-star-off"
                    plain
                    @click="star('npc', data.ID)"
                >
                    收藏数据
                </el-button>
                <el-button
                    v-else-if="isLogin"
                    class="u-button u-stared"
                    icon="el-icon-star-on"
                    plain
                    @click="cancelStar"
                >
                    取消收藏
                </el-button>
                <el-button
                    class="u-button u-to-dbm"
                    icon="el-icon-connection"
                    plain
                    @click="toDbm('npc', data.ID, data.Level)"
                >
                    在线构建
                </el-button>
            </div>
        </template>
    </div>
</template>
<script>
import itemMixin from "./mixin";
import props_npc from "@/assets/data/database/props_npc.json";

export default {
    name: "ItemNpc",
    mixins: [itemMixin],
    computed: {},
    methods: {
        propsFilter(key) {
            if (key === "IdKey") return false;
            if (this.data[key] === null) return false;
            if (props_npc?.[key]?.basic) return false;
            if (this.props_npc?.[key]?.adv && !this.hasRight) return false;
            return true;
        },
        showKeyLabel(key) {
            return props_npc?.[key]?.desc || key;
        },
    },
};
</script>
<style lang="less">
@import "~@/assets/css/database/item.less";
</style>
